          outline: none !important; }
  .footer-06 .aside-stretch {
    background: #322b3c; }
    .footer-06 .aside-stretch:after {
      background: #322b3c; }
  .footer-06 .aside-stretch-right {
    background: #372f42; }
    .footer-06 .aside-stretch-right:after {
      background: #372f42; }
